      "Самые крупные острова Индонезии — Новая Гвинея, Калимантан, Суматра, Сулавеси (Целебес) и Ява. Также в составе страны встречаются в огромном количестве одиночные скалы и коралловые острова. Индонезия расположена между Тихим океаном на северо-востоке и Индийским океаном на западе. Границы страны в основном морские. Протяженность береговой линии Индонезии составляет 54716 км. С северо-западной стороны Индонезию от Малайзии и Сингапура отделяет Малаккский пролив и Южно-Китайское море, на северо-востоке от Филиппин — море Сулу, от Палау — Тихий океан, от Австралии — Тиморское и Арафурское моря. Протяженность сухопутной границы Индонезии — 2830 км. На острове Калимантан Индонезия граничит с Малайзией, расположенной на севере острова. Восток острова Новая Гвинея принадлежит государству Папуа-Новая Гвинея, а на восточной части острова Тимор располагается государство Восточный Тимор.",
      "Основная территория Индонезии равнинная. Но из-за своего расположения на стыке двух тектонических зон большинство крупных островов имеют горные образования со множеством вулканов. Их насчитывают более 500, 220 из которых действующие. Эту область геологи называют Тихоокеанским поясом огня. На самом крупном острове Ява насчитывают 38 вулканов, самые известные из которых Семеру (3776 м), Мерапи (2891 м), Кава Иджен (2386 м), Бромо (2329 м), Папандаян (1800), Анак Кракатау (813 м). На острове Бали вулкан Агунг (3142 м), Батур (1717 м). Остров Суматра славится вулканом Керинчи (3800 м). Высочайшей точкой страны является гора Джая (5040 м) на острове Новая Гвинея. Благодаря обильным сезонным осадкам Индонезия имеет большое количество рек, многие из которых служат источниками электроэнергии. Самая большая река Индонезии находится на острове Калимантан и называется Капуас, ее протяженность 1040 км и площадь бассейна 97 тыс. кв. км.",
      "Береговая линия Индонезии довольно разнообразна: от коралловых рифов до песчаных пляжей. Внешние берега, омываемые индийским океаном, высокие и скалистые, в то время как внутренние расположены на низменностях с мангровыми зарослями и рисовыми полями.",
